<script>
import * as echarts from 'echarts';
import {getByFloor, getDetailInfo, getCondData, getMonitData, getAlarmData} from "@/api/iot/fanuc";
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

export default {
  name: "FanucMonitor",
  created() {
    this.timer = setInterval(() => {
      this.getFanucDataByFloor()
    }, 10000)
  },
  mounted() {
    this.getFanucDataByFloor()
  },
  computed: {
    fanucMachineInfo() {
      const pages = []
      const position = this.$route.query.position;
      if (this.$route.params.status) {
        this.setStatusRadioValue(this.$route.params.status);
        if (this.$route.params.status === 'default') {
          this.pushStatusRadioValue('01')
          this.pushStatusRadioValue('16')
          this.pushStatusRadioValue('11')
          this.pushStatusRadioValue('50')
          this.pushStatusRadioValue('00')
        }
        this.refreshPage()
      }
      this.setDefaultCount()
      const floor = position.substring(2, position.length)

      this.fanucMonitorInfo.forEach((item) => {
        if (item.monitMcName.indexOf(floor) === 0) {
          if (this.status[item.monitStatus]) {
            if (this.statusRadioValue.indexOf(item.monitStatus) > -1) {
              pages.push(item)
            }
            this.statusCount[item.monitStatus]++
          } else {
            if (this.statusRadioValue.indexOf("default") > -1) {
              pages.push(item)
            }
            this.statusCount.default++
          }
        }
      })
      return pages
    }
  },
  methods: {
    refreshPage(){
      this.$router.push(this.$route.fullPath)
    },
    setStatusRadioValue(status) {
      this.statusRadioValue = []
      this.statusRadioValue.push(status)
    },
    pushStatusRadioValue(status) {
      this.statusRadioValue.push(status)
    },
    setAllStatusRadioValue() {
      this.statusRadioValue = ['02', '00', '01', '17', '03', '16', '11', '50', '-1', 'default']
    },
    getStatusColor(statusCode) {
      return this.statusColor[statusCode] ? this.statusColor[statusCode] : this.statusColor.default
    },
    getStatusRadioColor(statusCode) {
      if (this.statusRadioValue.indexOf(statusCode) > -1)
        return this.statusColor[statusCode]
      else
        return "rgba(235,235,235,1)"
    },
    exportExcel(tableId, excelFileName) {
      const wb = XLSX.utils.table_to_book(document.querySelector(tableId));
      const wbOut = XLSX.write(wb, {bookType: 'xlsx', bookSST: true, type: 'array'});
      try {
        FileSaver.saveAs(new Blob([wbOut], {type: 'application/octet-stream'}), excelFileName)
      } catch (e) {
        if (typeof console !== 'undefined') console.log(e, wbOut)
      }
      return wbOut
    },
    getFanucDataByFloor() {
      getByFloor().then((response) => {
        const responseData = response.data
        if (responseData.code === '000000') {
          this.fanucMonitorInfo = responseData.data;
          setTimeout(() => { //echarts
            for (let fanucMachineInfoElement of this.fanucMachineInfo) {
              if (fanucMachineInfoElement.monitStatus === '-1')
                continue;
              this.refreshData(fanucMachineInfoElement, 'circleChart' + fanucMachineInfoElement.monitMcName)
            }
          }, 0)
        }
      })
    },
    onStatusRadioClick(statusCode) {
      const idx = this.statusRadioValue.indexOf(statusCode)
      if (idx > -1)
        this.statusRadioValue.splice(idx, 1)
      else
        this.statusRadioValue.push(statusCode)
    },
    onRadioClick() {
      this.fanucDialogCondData = []
      this.fanucDialogMonitData = []
    },
    getFanucDetailData() {
      getDetailInfo(this.dialogMachineName).then((response) => {
        const responseData = response.data
        if (responseData.code === '000000') {
          this.fanucDialogData = responseData.data;
        }
      })
    },
    checkMachineStatus(status) {
      return status !== '-1';
    },
    onCardClick(mcName) {
      this.dialogMachineName = mcName
      this.getFanucDetailData()
      this.dialogVisible = true
    },
    onDialogClosed() {
      this.fanucDialogCondData = []
      this.dateTimePickerValue = []
    },
    getStatusType(status) {
      return Object.prototype.hasOwnProperty.call(this.statusType, status) ? this.statusType[status] : "item_otherStatus";
    },
    getStatusName(status) {
      return Object.prototype.hasOwnProperty.call(this.status, status) ? this.status[status] : "";
    },
    getDialogMachineCondData() {
      if (this.dateTimePickerValue.length !== 2) {
        this.$message.error("");
      } else {
        const startTime = this.$moment(this.dateTimePickerValue[0]).format('YYYY-MM-DD HH:mm:ss');
        const endTime = this.$moment(this.dateTimePickerValue[1]).format('YYYY-MM-DD HH:mm:ss');
        getCondData(startTime, endTime, this.dialogMachineName).then((response) => {
          const responseData = response.data
          if (responseData.code === '000000') {
            this.fanucDialogCondData = responseData.data;
          }
        })
      }
    },
    getDialogMachineMonitData() {
      if (this.dateTimePickerValue.length !== 2) {
        this.$message.error("");
      } else {
        const startTime = this.$moment(this.dateTimePickerValue[0]).format('YYYY-MM-DD HH:mm:ss');
        const endTime = this.$moment(this.dateTimePickerValue[1]).format('YYYY-MM-DD HH:mm:ss');
        getMonitData(startTime, endTime, this.dialogMachineName).then((response) => {
          const responseData = response.data
          if (responseData.code === '000000') {
            this.fanucDialogMonitData = responseData.data;
          }
        })
      }
    },
    getDialogMachineAlarmData() {
      if (this.dateTimePickerValue.length !== 2) {
        this.$message.error("");
      } else {
        const startTime = this.$moment(this.dateTimePickerValue[0]).format('YYYY-MM-DD HH:mm:ss');
        const endTime = this.$moment(this.dateTimePickerValue[1]).format('YYYY-MM-DD HH:mm:ss');
        getAlarmData(startTime, endTime, this.dialogMachineName).then((response) => {
          const responseData = response.data
          if (responseData.code === '000000') {
            this.fanucDialogAlarmData = responseData.data;
          }
        })
      }
    },
    showPieChart() {
      const chartDom = document.getElementById('pieChart');
      const myChart = echarts.init(chartDom);
      let option;
      option = {
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b} : {c} %"
        },
        legend: {
          top: '5%',
          left: 'center'
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: 'center'
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '28',
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: this.getMoldData(this.fanucDialogData)
          }
        ],
        color: ["rgba(59,162,114,1)", "rgba(250,200,88,1)", "rgba(84,112,198,1)", "rgba(238,102,102,1)", "rgba(154,96,180,1)", "gray", "rgba(252,132,82,1)"]
      };
      myChart.setOption(option);
    },
    drawCircleChart(fanucData, chartId) {
      const chartDom = document.getElementById(chartId);
      const myChart = echarts.init(chartDom);
      let option;

      const listData = this.getMoldData(fanucData)

      option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {d}%'
        },
        legend: {
          itemWidth: 10,
          itemHeight: 10,
          icon: 'circle',
          x: 'right',
          y: 'center',
          orient: 'vertical', //
          align: 'left',       //icon
          itemGap: 3,         //
          padding: [0, 0, 0, 0], //
          bottom: '55%',       //
          formatter: function (name) {  //
            let value = 0;
            listData.forEach((item) => {
              if (item.name === name) {
                value = item.value;
              }
            })
            return `${name}  ${value}%`;  //+
          },
          textStyle: { //
            color: 'black',
            fontSize: 12,
          },
          data: ['', '', '', '', '', '', ''],
        },
        // graphic: [  //
        //   {
        //     tooltip: {
        //       trigger: 'item',
        //       formatter: ": " + this.status[fanucData.monitStatus]
        //     },
        //     elements: [{
        //       type: "circle",
        //       shape: {
        //         cx: 40,
        //         cy: 40,
        //         r: 20
        //
        //       },
        //       left: "12.3%",
        //       top: "39%",
        //       style: {
        //         fill: this.statusColor[fanucData.monitStatus],
        //       }
        //     }]
        //
        //   },
        // ],
        series: [{
          name: '',
          type: 'pie',
          radius: ['50%', '62%'],
          center: ['20%', '50%'],
          data: listData,
          label: false,
          labelLine: false,
        }
        ],
        color: ["rgba(59,162,114,1)", "rgba(250,200,88,1)", "rgba(84,112,198,1)", "rgb(238,102,102)", "rgba(154,96,180,1)", "gray", "rgba(252,132,82,1)"]
      }

      // setInterval(() => {
      //   const list = this.getMoldData(fanucData)
      //   myChart.setOption({
      //     legend: {
      //       formatter: function (name) {  //
      //         let value = 0;
      //         list.forEach((item) => {
      //           if (item.name === name) {
      //             value = item.value;
      //           }
      //         })
      //         return `${name}  ${value}%`;  //+
      //       }
      //     },
      //     series: [{
      //       data: list
      //     }]
      //   });
      // }, 10000);

      myChart.setOption(option);
    },
    refreshData(fanucData, chartId) {
      const chartDom = document.getElementById(chartId);
      const myChart = echarts.getInstanceByDom(chartDom)
      if (!myChart) {
        this.drawCircleChart(fanucData, chartId)
        return;
      }

      //
      const list = this.getMoldData(fanucData)
      myChart.setOption({
        legend: {
          formatter: function (name) {  //
            let value = 0;
            list.forEach((item) => {
              if (item.name === name) {
                value = item.value;
              }
            })
            return `${name}  ${value}%`;  //+
          }
        },
        series: [{
          data: list
        }]
      });
      // const option = myChart.getOption();
      // option.series[0].data = data;
      // myChart.setOption(option);
    },
    getMoldData(fanucData) {
      const moldData = [];
      const mold_automate = fanucData.moldData.mold_automate.replace('%', '').trim()
      const mold_wait = fanucData.moldData.mold_wait.replace('%', '').trim()
      const mold_manual = fanucData.moldData.mold_manual.replace('%', '').trim()
      const mold_alarm = fanucData.moldData.mold_alarm.replace('%', '').trim()
      const mold_complete = fanucData.moldData.mold_complete.replace('%', '').trim()
      const mold_shutdown = fanucData.moldData.mold_shutdown.replace('%', '').trim()
      const mold_other = (100.00 - mold_automate - mold_wait - mold_manual - mold_alarm - mold_complete - mold_shutdown).toFixed(2)
      moldData.push({value: mold_automate, name: ''})
      moldData.push({value: mold_wait, name: ''})
      moldData.push({value: mold_manual, name: ''})
      moldData.push({value: mold_alarm, name: ''})
      moldData.push({value: mold_complete, name: ''})
      moldData.push({value: mold_shutdown, name: ''})
      moldData.push({value: mold_other, name: ''})
      return moldData;
    },
    setDefaultCount() {
      this.statusCount = {
        '02': 0,
        '00': 0,
